@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body{background-color:#1A1A1A}.root{display:grid;grid-template-columns:1fr 1fr;place-items:center;color:#E4E4E4;font-family:'Noto Sans', sans-serif;overflow:hidden}@media screen and (min-width: 992px){.root{display:block;padding:104px 94px}}ul{list-style:none}a{margin-bottom:5px;font-size:18px;line-height:24px;color:#E4E4E4;text-decoration:underline;transition:all 0.6s ease}a:hover{color:#b1b1b1}.page-link{font-size:20px;line-height:27px;font-weight:700;text-decoration:none;position:absolute;top:30px;display:flex;align-items:center;justify-content:center}.page-link span{margin-right:2px}@media screen and (min-width: 992px){.page-link{display:none}}.page-link-right{right:20px}.page-link-right span{margin-right:6px}.page-link-left{left:20px}.page-link-left span{margin-left:6px}.error{display:none}.profile{padding-left:20px;position:relative;display:flex;flex-direction:column;justify-content:center;width:100vw;height:100vh}.profile .h2{display:none}@media screen and (min-width: 992px){.profile{margin-bottom:40px;width:auto;height:auto}}.profile-nav{margin-top:20px}.profile .location{font-size:14px;opacity:0.6}.projects{padding-left:20px;position:relative;display:flex;flex-direction:column;justify-content:center;width:100vw;height:100vh}@media screen and (min-width: 992px){.projects{width:auto;height:auto}}.projects--description{font-size:14px;line-height:19px;opacity:0.4}.projects--link{margin:0;font-size:20px;line-height:27px}.projects--link-code{margin:0;font-size:12px;line-height:19px;opacity:0.4}.projects-nav{margin-top:20px}.projects-nav li{margin-bottom:16px}.title{margin:0;font-family:'Noto Sans', sans-serif;font-weight:700}.subtitle{font-family:'Noto Sans', sans-serif;font-weight:400;font-size:16px;line-height:22px}@media screen and (min-width: 992px){.subtitle{font-size:20px;line-height:27px}}.h1{font-size:30px;line-height:41px}@media screen and (min-width: 992px){.h1{font-size:40px;line-height:55px}}.h2{font-size:28px;line-height:38px}@media screen and (min-width: 992px){.h2{font-size:30px;line-height:41px}}

